.video {
  position: relative;
  background-color: #5e6178;
  width: 100vw;
  height: 100vh;
}

.video_player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  video {
    width: 100%;
    height: auto;
    position: relative;
    top: 0;
    left: 0;
  }
}


